










import Vue from 'vue';
import { Auth } from '@aws-amplify/auth';

export default Vue.extend({
  data() {
    return {
      isSignedIn: false,
    };
  },
  mounted() {
    this.setStatus();
  },
  methods: {
    async onSignOut(event: Event) {
      event.preventDefault();

      await Auth.signOut();
    },
    async setStatus() {
      this.isSignedIn = await Auth.currentAuthenticatedUser();
    },
  },
});
